.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}

.service {
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
  background: var(--color-primary);
  border-color: color(--color-primary-variant);
  cursor: default;
}

.service__head {
  background: var(--color-primary-variant);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}

.service__head h3 {
  color:var(--color-bg);
  font-size: 1rem;
  text-align: center;
}

.service__list {
padding: 2rem;
}

.service__list li {
  display: flex;
  gap: 1rem;
  margin-bottom:0.8rem;

  }

  .service__list-icon {
    color: var(--color-primary-variant);
    margin-top: 2px;
/* OPTIONAL FOR CHECKMARK SIZE */
    font-size: 2rem;
    }

  .service__list p {
    font-size: 0.9rem;
  }

/* ======= Media Queries for Website Responsiveness (Medium Devices) tablets ====== */

@media screen and (max-width: 1024px) {
  .services__container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .service {
    height: auto;
  }
  }
  
  /* ======= Media Queries for Website Responsiveness (Small Devices) phones ====== */
  
  @media screen and (max-width: 600px) {
    .services__container {
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }
  }