header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

h1 {
    color: rgb(255, 136, 57);
    font-family: Verdana, Geneva, Tahoma, sans-serif;;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ======== CTA ========= */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ======== Header Socials ========= */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position:absolute;
    left: 0;
    bottom: 0.3rem;
}

.header__socials a:hover {
    color:var(--color-primary-variant);
}

.header__socials::after {
    content:'';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ======== Me ========= */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 24rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 12rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ======== Scroll Down ========= */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

.scroll__down:hover {
    color: var(--color-primary-variant);
}

/* ======= Media Queries for Website Responsiveness (Medium Devices) tablets ====== */

@media screen and (max-width: 1024px) {
 header {
    height: 100vh;
 }
}

/* ======= Media Queries for Website Responsiveness (Small Devices) phones ====== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
     }
     .header__socials, .scroll__down {
        display: none;
     }
}